.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    z-index: 9999;
    &__composition {
        position: absolute;
        top: 50%;
        left: 50%;
        transform:translate3d(-50%, -50%, 0);
    }
    &__keyhole {
        position: absolute;
        top: 50%;
        left: 50%;
        transform:translate3d(-50%, -25%, 0);
    }
}