.header-container {
    max-width: 1350px;
    margin: 0 auto;
    padding: 50px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 90px;
}

.logo {
    display: flex;
    align-items: center;
    &__text {
        margin-left: 21px;
        @media (max-width: 550px) {
            margin-left: 8px;
        }
    }
    &__img {
        width: 100px;
        height: 100px;
        display: flex;
        img {
            width: 100%;
            object-fit: contain;
        }
        @media (max-width: 550px) {
            width: 80px;
            height: 80px;
        }
    }
    @media (max-width: 550px) {
        max-width: 280px;
    }
}

.theme-switcher {
    width: 40px;
    height: 40px;
    background: url(../img/th.svg) center no-repeat;
    background-size: contain;
    cursor: pointer;
    transform: rotate(180deg);
    transition: transform;
    transition-duration: .3s;
    body.dark-theme & {
        transform: rotate(0);
    }
    @media (max-width: 550px) {
        width: 30px;
        height: 30px;
    }
}